    <div>
        <div class="legends-checkbox-header">
            <el-text style="font-size:20pt;">组件选择</el-text>
        </div>
        <div class="buttons-container">
            <!-- <el-button style="width:30%" @click="develop" v-show="collapsed">展开</el-button>
            <el-button style="width:30%" @click="develop" v-show="!collapsed">折叠</el-button> -->
            <el-button style="width:150px;height:40px;" @click="sendParent" type="primary">点击提交</el-button>
        </div>
    </div>
    <img src="../assets/images/横线2.svg" alt="" style="width: 100%; display:block;padding: 8px;"/>
    <div class="legendsArea">
        shortname:
        <el-button @click="selectAllShortname">全选</el-button> 
        <el-button @click="cancelSelectAllShortname">取消全选</el-button>
        <el-select
            v-model="shortname"
            multiple
            collapse-tags
            collapse-tags-tooltip
            placeholder="请选择"
            style="width: 300px;padding-left: 20px;border:none;"
            >
            <el-option
                v-for="(mshortname,index) in mlegendsList[0].shortname"
                :key=index
                :label='mshortname'
                :value=mshortname
            />
        </el-select>
        <!-- <div v-if="!collapsed">
            <label v-for="(mshortname,index) in mlegendsList[0].shortname.slice(0,this.maxCheckBoxs)" :key=index >
                <el-checkbox-button class="elcheckbox" v-model="shortname" :value=mshortname :label='mshortname' border ></el-checkbox-button>
            </label>
            <label v-for="(mshortname, index) in remainingShortnames" :key="index">
                <span style="display: inline-block; width:0%" v-if="index%6==0"></span>
                <el-checkbox-button class="elcheckbox" v-model="shortname" :value=mshortname :label='mshortname' border ></el-checkbox-button>
                <br v-if="index%6==5">
            </label>
        </div> -->
        <br><br>
        type:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-button @click="selectAllType">全选</el-button>
        <el-button @click="cancelSelectAllType">取消全选</el-button>
        <el-select
            v-model="type"
            multiple
            collapse-tags
            collapse-tags-tooltip
            placeholder="请选择"
            style="width: 300px;padding-left: 20px;"
            >
            <el-option
                v-for="(mtype,index) in mlegendsList[0].type"
                :key=index
                :label='mtype'
                :value=mtype
            />
        </el-select>
        <!-- <div v-if="!collapsed">
            <label v-for="(mtype,index) in mlegendsList[0].type.slice(0,this.maxCheckBoxs)" :key=index >
                <el-checkbox-button class="elcheckbox" v-model="type" :value=mtype :label='mtype' border></el-checkbox-button>
            </label>
            <label v-for="(mtype, index) in remainingTypes" :key="index">
                <span style="display: inline-block; width:0%" v-if="index%6==0"></span>
                <el-checkbox-button class="elcheckbox" v-model="type" :value=mtype :label='mtype' border ></el-checkbox-button>
                <br v-if="index%6==5">
            </label>
        </div> -->
        <br><br>
        version:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <el-button @click="selectAllVersion">全选</el-button>
        <el-button @click="cancelSelectAllVersion">取消全选</el-button>
        <el-select
            v-model="version"
            multiple
            collapse-tags
            collapse-tags-tooltip
            placeholder="请选择"
            style="width: 300px;padding-left: 20px;"
            >
            <el-option
                v-for="(mversion,index) in mlegendsList[0].version"
                :key=index
                :label='mversion'
                :value=mversion
            />
        </el-select>
        <!-- <div v-if="!collapsed">
            <label v-for="(mversion,index) in mlegendsList[0].version.slice(0,this.maxCheckBoxs)" :key=index >
                <el-checkbox-button class="elcheckbox" width="100px" v-model="version" :value=mversion :label='mversion' border></el-checkbox-button>
            </label>
            <label v-for="(mversion, index) in remainingVersions" :key="index">
                <span style="display: inline-block; width:0%" v-if="index%6==0"></span>
                <el-checkbox-button class="elcheckbox" v-model="version" :value=mversion :label='mversion' border ></el-checkbox-button>
                <br v-if="index%6==5">
            </label>
        </div> -->
        <br>
    </div>
</div>
</template>

<style>
.legends-checkbox-container{
    width: 98%;height:15%;
    margin-left: 1%;
    border: 10px transparent solid;
    border-radius:40px;
    position: relative;
    background-color: black;
}
.select-trigger,.el-input,.el-input--suffix,.el-input__wrapper,.el-select-dropdown{
    background-color: #2d1c2b;
    border: none;
}

@media screen and (min-width:768px) {
    .legends-checkbox-header{
        width: 10%;
        height:100%;
        float:left;
        color:aliceblue;
        display:flex;
        justify-content: center;
        align-items: center;
        
    }
    .buttons-container{
        width: 20%;
        height:100%;
        float:right;
        color:aliceblue;
        display:flex;
        justify-content: center;
        align-items: center;
    }
    .legendsArea{
        width: 96%;
        padding-left: 2%;
        margin-left: 1%;
        white-space: nowrap; 
        overflow-x: hidden;
        text-align: left;
        color:rgb(244, 247, 250);
    }
    button{
        width: 100px;
        height: 30px;
        margin-left: 3%;
    }
    .elcheckbox{
        margin-left: 1.5%;
        margin-bottom: 1%;
    }
    .el-button{
        border-radius: 5vw;
        background: #ff7babc2;
        color: white;
        border: none;
    }
}
@media screen and (max-width:768px) {
    .legends-checkbox-header{
        width: 25%;
        height:100%;
        float:left;
        color:aliceblue;
        font-size: 0.8em;
        display:flex;
        justify-content: center;
        align-items: center;
        padding-top: 10px;
    }
    .buttons-container{
        width: 60%;
        height:100%;
        float:right;
        color:aliceblue;
        display:flex;
        justify-content:flex-end;
        align-items: center;
    }
    .legendsArea{
        width: 96%;
        padding-left: 2%;
        padding-top: 1.5%;
        padding-bottom: 15px;
        margin-left: 5%;
        overflow-x: hidden;
        text-align: left;
        /* z-index: 500;
        position: relative; */
        background-color: black;
        color:rgb(244, 247, 250);
    }
    .el-button{
        margin-left: 3%;
        font-size: 0.7em;
        width: 80px;
        height: 30px;
    }
    .elcheckbox{
        margin-left: 1.5%;
        margin-bottom: 1%;
    }
    .el-input__wrapper{
        background-color: #2d1c2b;
    }
}
</style>

<script>
    export default {
        data(){
            return {
                collapsed:true,
                maxCheckBoxs : 5,
                version:[],
                versionCollapsed: true,
                shortname:[],
                shortnameCollapsed: true,
                type:[],
                typeCollapsed:true,
                updatelist:[],
            }
        },
        computed: {
            remainingShortnames() {
                if (this.mlegendsList[0].shortname.length <= this.maxCheckBoxs) {
                    return [];
                } else {
                    return this.mlegendsList[0].shortname.slice(this.maxCheckBoxs);
                }
            },
            remainingTypes() {
                if (this.mlegendsList[0].type.length <= this.maxCheckBoxs) {
                    return [];
                } else {
                    return this.mlegendsList[0].type.slice(this.maxCheckBoxs);
                }
            },
            remainingVersions() {
                if (this.mlegendsList[0].version.length <= this.maxCheckBoxs) {
                    return [];
                } else {
                    return this.mlegendsList[0].version.slice(this.maxCheckBoxs);
                }
            },
            shortnameShowCollapse() {
                return this.remainingShortnames.length > 0;
            },
            typeShowCollapse() {
                return this.remainingTypes.length > 0;
            },
            versionShowCollapse() {
                return this.remainingVersions.length > 0;
            },
        },
        props:{
            mlegendsList:Object,
        },  
        methods: {
            sendParent:function(){
                this.updatelist = [{
                    "version":this.version,
                    "shortname":this.shortname,
                    "type":this.type,
                }]
                this.$emit('updateLegendList',this.updatelist);
            },
            selectAllShortname:function(){
                this.shortname = this.mlegendsList[0].shortname;
            },
            selectAllType:function(){
                this.type = this.mlegendsList[0].type;
            },
            selectAllVersion:function(){
                this.version = this.mlegendsList[0].version;
            },
            cancelSelectAllShortname:function(){
                this.shortname = [];
            },
            cancelSelectAllType:function(){
                this.type = [];
            },
            cancelSelectAllVersion:function(){
                this.version = [];
            },
            shortnameToggleCollapse() {
                this.shortnameCollapsed = !this.shortnameCollapsed;
            },
            typeToggleCollapse() {
                this.typeCollapsed = !this.typeCollapsed;
            },
            versionToggleCollapse() {
                this.shortnameCollapsed = !this.versionCollapsed;
            },
            develop() {
                this.collapsed = !this.collapsed;
            },
        },
    }
</script>


