<template>
    <div class="forceOrientationDiagram-container"></div>
</template>

<style>
    .forceOrientationDiagram-container {
        /* background-color: pink; */
        height: 100%;
        width: 99%;
        border: 4px solid;
        border-image: linear-gradient(to right, #8f41e9, #578aef) 1;
        fill:aliceblue;
        /* overflow-y: scroll; */
    }
</style>

<script>
    import * as d3 from 'd3';
    // import * as d3 from "../../../public/d3.v3.min.js";
    export default{
        props:{
            graphdata:Object,
        },  
        data(){
            return{
                listenNode:"information"
            }
            
        },
        watch:{
            listenNode (t) {
                console.log("子组件data绑定change!");
                this.$emit('nodeclick', t)//主要是通过$emit方法来实现传参的方式，第一个参数是自定义事件名称，第二个则是要传的数据
            },
            graphdata:{
                deep:true,
                handler(data){
                    console.log(data);
                    d3.select(".forceOrientationDiagram").remove();
                    var nodes = [];
                    var edges = [];
                    var maketypes = [];    //存放厂商，用于制作图例
                    var textstrs = [];     //存放切割后的文本，用于实现分行展示
                    for(var i = 0;i<data.nodes.length;i++){
                        //获得node
                        nodes.push({
                            name:data.nodes[i].name,
                            shortname:data.nodes[i].shortname,
                            type:data.nodes[i].type,
                            version:data.nodes[i].version,
                            maketype:data.nodes[i].maketype,
                            links:0,  //用于记录相邻边数，确定节点大小
                        })
                        //获得textstrs，每个节点信息存放一个数组，数组内多个字符串为要展示的每行信息
                        textstrs.push(["name:"+nodes[i].name,"shortname:"+nodes[i].shortname,
                                    "type:"+nodes[i].type,"version"+nodes[i].version,
                                "maketype:"+nodes[i].maketype]);
                        //获得所有的厂商
                        if(maketypes.indexOf(nodes[i].maketype)==-1)
                            maketypes.push(nodes[i].maketype);
                    }
                    for(i = 0;i<data.links.length;i++){
                        //构造符合条件的edges信息
                        var s=-1,t=-1;
                        var flag = 0;    //用来记录查询的节点位置
                        while(s<0||t<0){
                            if(nodes[flag].name == data.links[i].source)
                                s = flag;
                            if(nodes[flag].name == data.links[i].target)
                                t = flag;
                            flag++;
                        }
                        nodes[s].links++; nodes[t].links++;
                        edges.push({
                            source:s,
                            target:t,
                        })
                    }
                    // function zoomed() {
                    //     svg.attr("transform",
                    //         "translate(" + zoom.translate() + ")" +
                    //         "scale(" + zoom.scale() + ")"
                    //     );
                    // }
                    //关于颜色的一些设置
                    var origincolor = "skyblue";
                    //基本信息
                    var marge = {top:0,bottom:60,left:0,right:60};
                    // var zoom = d3.behavior.zoom().scaleExtent([1, 8]).on("zoom", zoomed);
                    
                    var svg = d3.select(".forceOrientationDiagram-container")
                        .append("svg")
                        .attr("class","forceOrientationDiagram")
                        .attr("preserveAspectRatio", "xMidYMid meet")
                        .attr("viewBox","0,0,500,500")
                        .attr("width","100%")
                        .attr("height","100%");
                    var width = 500;
                    var height = 500;
                    var g = svg.append("g").attr("transform","translate("+marge.left+","+marge.top+")");
                    //定义一个颜色比例尺
                    var color = d3.scaleOrdinal(d3.schemeCategory10);
                    //定义拖拽效果
                    var drag = d3.drag()
                            .on("start",started)
                            .on("drag",dragged)
                            .on("end",ended);

                    //新建一个力导向图
                    var forceSimulation = d3.forceSimulation(nodes)
                        .force("charge",d3.forceManyBody())
                        .force("link",d3.forceLink(edges))
                        .force("center",d3.forceCenter());
                    //生成节点数据
                    forceSimulation.nodes(nodes).on("tick",ticked);
                    //生成边数据
                    forceSimulation.force("link")
                        .links(edges)
                        .distance(function(){
                            return 10;
                        })
                    //设置图形的中心位置
                    forceSimulation.force("center")
                        .x(width/2)
                        .y(height/2);
                    forceSimulation.alphaDecay(0.05) // 衰减系数，值越大，图表稳定越快
                    forceSimulation.force('charge')
                        .strength(-20) // 排斥力强度，正值相互吸引，负值相互排斥

