                    var links = g.append("g")
                        .selectAll("line")
                        .data(edges)
                        .enter()
                        .append("line")
                        .attr("stroke",origincolor)
                        .attr("class",function(d){
                            return "links"+d.index;
                        })
                        .attr("stroke-width",1);
                    //绘制节点
                    var gs = g.selectAll(".circleText")
                        .data(nodes)
                        .enter()
                        .append("g")
                        .attr("transform",function(d){
                            var cirX = d.x;
                            var cirY = d.y;
                            return "translate("+cirX+","+cirY+")";
                        })
                        .attr("class",function(d){return "gs"+d.index;})       
                    const text = gs.append("text")
                        .attr("class",function(d){
                            return "text"+d.index;
                        })
