                            .enter()
                            .append("tspan")
                            .attr("x",text.attr("x"))
                            .attr("dy","1em")
                            .text(function(d){
                            return d;
                            });
                    }
                    gs.append("circle")
                        .attr("class",function(d){
                            return "circle"+d.index;
                        })
                        .attr("r",function(d){
                            //根据边的数目设置半径
                            return 5+d.links/50;
                        })
                        .attr("fill",function(d){
                            //根据厂商确定节点颜色
                            return color(maketypes.indexOf(d.maketype));
                        })
                        .on("mouseover",function(d){
                            //将鼠标放上节点
                            //显示节点文本信息
                            d3.select(".text"+d.target.__data__.index).attr("visibility","visible");
                            for(var i = 0;i<edges.length;i++){
                                //将节点放大，相邻节点略微放大，邻边加粗
                                if(edges[i].source.name==d.target.__data__.name){
                                    d3.select(".circle"+edges[i].source.index).attr("r","20");
                                    d3.select(".circle"+edges[i].target.index).attr("r","13");
                                    d3.select(".links"+edges[i].index).attr("stroke-width","5")
                                }
                                if(edges[i].target.name==d.target.__data__.name){
