                            for(var i = 0;i<edges.length;i++){
                                d3.select(".circle"+edges[i].source.index).attr("visibility","hidden");
                                d3.select(".circle"+edges[i].target.index).attr("visibility","hidden");
                                d3.select(".links"+edges[i].index).attr("visibility","hidden");
                            }
                            for(i = 0;i<edges.length;i++){
                                if(edges[i].source.maketype==theMakeType||edges[i].target.maketype==theMakeType){
                                    d3.select(".circle"+edges[i].source.index).attr("visibility","visible");
                                    d3.select(".circle"+edges[i].target.index).attr("visibility","visible");
                                    d3.select(".links"+edges[i].index).attr("visibility","visible");
                                    if(edges[i].source.maketype!=theMakeType){
                                        d3.select(".circle"+edges[i].source.index).style("opacity",0.5); //将相关但非该图例点太透明度降低
                                    }
                                    if(edges[i].target.maketype!=theMakeType){
                                        d3.select(".circle"+edges[i].target.index).style("opacity",0.5);
                                    }
                                }
                            }
                        })
                        .on("mouseout",function(d){
                            //鼠标离开图例
                            //恢复显示
                            for(var i = 0;i<nodes.length;i++)
                                d3.select(".circle"+nodes[i].index).attr("visibility","visible");
                            for(i = 0;i<edges.length;i++){
                                d3.select(".links"+edges[i].index).attr("visibility","visible");
                            }
                            //恢复透明度
                            var theMakeType = d.srcElement.__data__.maketype;
                            for(i = 0;i<edges.length;i++){
                                if(edges[i].source.maketype==theMakeType&&edges[i].target.maketype==theMakeType){
                                    if(edges[i].source.maketype!=theMakeType){
                                        d3.select(".circle"+edges[i].source.index).style("opacity",1.0);
                                    }
                                    if(edges[i].target.maketype!=theMakeType){
                                        d3.select(".circle"+edges[i].target.index).tyle("opacity",1.0);
                                    }
                                }
                            }
                        });
                    //添加图例方块
                    legend.append("rect")
                        .attr("x",width-25)
                        .attr("y",108)
                        .attr("width",30)
                        .attr("height",10)
                        .attr("fill",function(d){
                            return color(maketypes.indexOf(d.maketype));
                        });
                    //添加图例文字
                    legend.append("text")
                        .attr("x",width-30)
                        .attr("y",120)
                        .attr("font-size",10)
                        .style("text-anchor","end")
                        .text(function(d){
                            return d.maketype;
                        }); */



                    function ticked()
                    {
                        links
                            .attr("x1",function(d){return d.source.x;})
                            .attr("y1",function(d){return d.source.y;})
                            .attr("x2",function(d){return d.target.x;})
                            .attr("y2",function(d){return d.target.y;});
                        
                        gs
                            .attr("transform",function(d) { return "translate(" + d.x + "," + d.y + ")"; });
                    }
                    //拖拽效果使用到的三个函数
                    function started(d) {
                        if (!d.active) {
                            forceSimulation.alphaTarget(0.8).restart();
                        }
                        d.subject.fx = d.subject.x;
                        d.subject.fy = d.subject.y;
                    }
                    function dragged(d) {
                        d.subject.fx = d.x;
                        d.subject.fy = d.y;
                    }
                    function ended(d) {
                        if (!d.active) {
                            forceSimulation.alphaTarget(0);
                        }
                        d.subject.fx = null;
                        d.subject.fy = null;
                    }
                }
            }
