<template>
    <div class="detail">
        <div class="legendbox-container">
            <LegendCheckBox @updateLegendList="changeLegendList" :mlegendsList="legendlist"></LegendCheckBox>
        </div>
        <div class="upper-container" >
            <div  class="ForceOrientationDiagram-container">
                <ForceOrientationDiagram  @nodeclick="nodeclick" :graphdata = "updatedRelation"></ForceOrientationDiagram>
            </div>
        </div>
        <!-- 占位 -->
        <div style="height: 20px;"></div>
        <div class="detailInformation-container">
            <div class="backstudent">
                <h2 >详细信息</h2>
                <h3 v-for="(CVE,key) in CVES[clickWhat] " id="detailText"  style="color: white;"> <span style="color: #ff7bac;">{{key}}</span>:{{CVE}}</h3>

            </div>
        </div>
        <div style="width:95%;margin-top: 1.5%;margin:auto;">
            <div class="table">
                <table>
                    <caption style="text-align: left;"> 组件相关漏洞 </caption>
                    <thead>
                        <tr style="background: linear-gradient(to right, #7400bfcc, #ff7baccc);">
                            <th>序号</th>
                            <th v-for="item in showtable">{{ item }}</th>
                            <th>查看详情</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(CVE,index) in currentPageCVEs ">
                            <td >{{ CVE.index+1 }}</td>
                            <td v-for="item in showtable">{{ CVE[item] }}</td>
                            <td><button class="button1" @click="greet(index)"><span>详细信息</span></button></td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div style="display: flex; flex-direction: row; justify-content: center;align-items: center;padding-top: 20px;padding-bottom: 20px;width:100%;">
            <el-button @click="lastPage()">上一页</el-button>
            <el-button @click="nextPage()">下一页</el-button>
            <div style="display: flex;flex-direction: row;color:white;align-items: center;padding-left:10px;">
                <span>每页</span>
                <el-input v-model="userInputItemsPerPage" width="100"></el-input>
                <span>条</span>
            </div>
            <el-button @click="this.itemsPerPage = this.userInputItemsPerPage">确认更改</el-button>
        </div>
        
       
    </div >
</template>
<script setup>
    import LegendCheckBox from '@/components/LegendCheckBox.vue';
    import ForceOrientationDiagram from '@/components/graphs/ForceOrientationDiagram.vue';
    import axios from 'axios';
</script>
<script>
export default {      
    data() {
        return{
            
            whatnode:"i don't know",//哪个node被点击,此为接口，已经传值成功
            legendlist:[{
                version:[],
                shortname:[],
                type:[],
            }],
            /* 定义一个updatedLegendList用来存放更新后的数据 */
            updateLegendList:[],
            relation:[],
            updatedRelation:[],
            clickWhat:0,
            showtable:['ID','vendor','danger','fix','publishedDate'],
            CVES:
            [{
                index:0,
                ID:'CVE-2011-1498',
                vendor:'apache',
                danger:'LOW',
                fix:'null',
                publishedDate:'2011-07-07',
                description:"Apache HttpClient 4.x before 4.1.1 in Apache HttpComponents, when used with an authenticating proxy server, sends the Proxy-Authorization header to the origin server, which allows remote web servers to obtain sensitive information by logging this header. "
                },{
                index:1,
                ID:'CVE-2012-5783',
                vendor:'cve@mitre.org',
                danger:'LOW',
                fix:'null',
                publishedDate:'2012-11-04',
                description:"Apache Commons HttpClient 3.x, as used in Amazon Flexible Payments Service (FPS) merchant Java SDK and other products,  does not verify that the server hostname matches a domain name in the subject's Common Name (CN) or subjectAltName field of the X.509 certificate, which allows man-in-the-middle attackers to spoof SSL servers via an arbitrary valid certificate. "
                },{
                index:2,
                ID:'CVE-2013-4366',
                vendor:'secalert@redhat.com ',
                danger:'HIGH',
                fix:'null',
                publishedDate:'2017-10-30',
                description:"http/impl/client/HttpClientBuilder.java in Apache HttpClient 4.3.x before 4.3.1 does not ensure that X509HostnameVerifier is not null, which allows attackers to have unspecified impact via vectors involving hostname verification."
                },{
                index:3,
                ID:'CVE-2014-3577',
                vendor:'secalert@redhat.com ',
                danger:'LOW',
                fix:'null',
                publishedDate:'2014-08-21',
                description:"org.apache.http.conn.ssl.AbstractVerifier in Apache HttpComponents HttpClient before 4.3.5 and HttpAsyncClient before 4.0.2 does not properly verify that the server hostname matches a domain name in the subject's Common Name (CN) or subjectAltName field of the X.509 certificate, which allows man-in-the-middle attackers to spoof SSL servers via a 'CN='' string in a field in the distinguished name (DN) of a certificate, as demonstrated by the 'foo,CN=www.apache.org' string in the O field. "
                },/* {
                ID:'',
                vendor:'',
                danger:'',
                fix:'',
                publishedDate:'',
                description:""
                } */],
                itemsPerPage: 20,
                userInputItemsPerPage:"",
                currentPage:1
        }
    },
    computed: {
        currentPageCVEs() {
            const startIndex = (this.currentPage - 1) * this.itemsPerPage;
            const endIndex = startIndex + this.itemsPerPage;
            return this.CVES.slice(startIndex, endIndex);
        },
        totalPages() {
            return Math.ceil(this.CVES.length / this.itemsPerPage);
        },
    },
    watch:{
        clickWhat (t) {
            console.log(t,"change!");
        },
        whatnode(t){
            console.log("whatnode",t,"change!");
        },
        updateLegendList(newvalue){
            //监听updatedLegendList，同时对传入力导向图的数据进行筛选
            console.log("复选框传值成功",newvalue);
            //筛选this.relation
            //筛选nodes
            var newnodes;
            var newnames;
            var newlinks;
            newnodes = [];
            newnames = [];
            newlinks = [];
            for(var i = 0;i<this.relation.nodes.length;i++){
                if(this.updateLegendList[0].shortname.indexOf(this.relation.nodes[i].shortname)!=-1&&this.updateLegendList[0].type.indexOf(this.relation.nodes[i].type)!=-1&&this.updateLegendList[0].version.indexOf(this.relation.nodes[i].version)!=-1){
                    newnodes.push(this.relation.nodes[i]);
                    newnames.push(this.relation.nodes[i].name);
                }
            }
            for(i = 0;i<this.relation.links.length;i++){
                if(newnames.indexOf(this.relation.links[i].source)!=-1&&newnames.indexOf(this.relation.links[i].target)!=-1){
                    newlinks.push(this.relation.links[i]);
                }
            }
            this.updatedRelation.nodes = newnodes;
            this.updatedRelation.links = newlinks;
        }
    },
    mounted(){
        var __this=this;
        //axios.get('../fake.json').then((res) => {
        // axios.get('http://120.26.95.9:8081/console/detail').then((res) => {
            axios.get('../dubbo-2.6.11-frontend.json').then((res) => {
            __this.relation=res.data;
            console.log("读取完成",__this.relation);
            //提取legends信息
            for(var i = 0;i<__this.relation.nodes.length;i++){
                if(this.legendlist[0].shortname.indexOf(__this.relation.nodes[i].shortname)==-1){
                    this.legendlist[0].shortname.push(__this.relation.nodes[i].shortname);
                }
                if(this.legendlist[0].type.indexOf(__this.relation.nodes[i].type)==-1){
                    this.legendlist[0].type.push(__this.relation.nodes[i].type);
                }
                if(this.legendlist[0].version.indexOf(__this.relation.nodes[i].version)==-1){
                    this.legendlist[0].version.push(__this.relation.nodes[i].version);
                }
            }
            const deepCopy = (obj) => JSON.parse(JSON.stringify(obj));
            this.updatedRelation = deepCopy(this.relation);
            this.updateLegendList = this.legendlist;
        })
        
    },
    methods: {
        greet(d){
            this.clickWhat=d;
        },
        nodeclick(t){
            this.whatnode=t;
        },
        changeLegendList(value){
            this.updateLegendList = value;
        },
        lastPage(){
            if(this.currentPage>1)
                this.currentPage--;
        },
        nextPage(){
            if(this.currentPage<this.totalPages)
                this.currentPage++;
        }
    },
    created() {
        axios.get('../dubbo-2.6.11-frontend.json').then((res) => {
        let len = res.data.report.length;
        let datax = res.data.report;
        for(let i = 0; i < len; i++){
            if(i >= 4){
                const newcve = {
                    ID: datax[i].CVE_ID,
                    vendor: datax[i].vendor,
                    danger: datax[i].danger,
                    fix: datax[i].fix,
                    publishedDate: datax[i].publishedDate,
                    description: datax[i].CVEDescription
                };
                this.CVES.push(newcve);
            }else{
                this.CVES[i].ID = datax[i].CVE_ID;
                this.CVES[i].vendor = datax[i].vendor;
                this.CVES[i].description = datax[i].CVEDescription;
                this.CVES[i].danger = datax[i].danger;               
                this.CVES[i].fix = datax[i].fix;
                this.CVES[i].publishedDate = datax[i].publishedDate;
            }
        }
        for(let i = 0;i<this.CVES.length;i++)
            this.CVES[i].index = i;
})

}
        
}
</script>

<style>
@media screen and (min-width: 768px) {
    .legends-container{
        height:38%;width:100%;
    }
    .upper-container{
        height:50%;width:100%;margin-top: 1.5%;
    }
    .ForceOrientationDiagram-container{
        height: 100%;width:95%;margin: auto;
    }
    .detailInformation-container{
        width:95%;margin: auto;
    }
    #detailText{
        padding: 2px;
        text-align:left;
        color:#000000;
    }
    .backstudent{
        background-color: #453350;
        fill:aliceblue;
        color: #fff;
        /* overflow-y: scroll; */
        height:100%;
        border-radius: 20px;
        padding: 1.5%;
        background-image: linear-gradient(to right, #3c4ffc30, #b437fc30);
    }

    .detail{
        width: 95%;
        height: 150%;
        margin: auto;
    }
    .upperPart{
        width:100%;
        height: 55%;
    }
    .lowerPart{
        width: 100%;
        height: 45%;
        overflow-y:scroll;
        overflow-x:hidden;
    }
    .table{
        width: 100%;
        height: 90%;
        margin-top: 1%;
        /* background-color: rgb(253, 253, 253); */
    }
    .force-orientedDiagram{
        width: 50%;
        height: 100%;
        float: left;
    }
    .graphicLinkagePart{
        width: 45%;
        height: 100%;
        float: right;
        margin-left: 5%;
    }
    .legends,.briefInformation{
        width: 100%;
        height: 45%;
        margin-top: 2%;
        margin-bottom: 2%;
        /* background-color: pink; */
        overflow-y:scroll;
        overflow-x:hidden;
        border: 4px solid;
        border-image: linear-gradient(to right, #8f41e9, #578aef) 1;
    }

table{
    width: 100%;
    border-collapse: collapse;
    background: linear-gradient(to right, #7400bfcc, #ff7baccc);
    opacity: 1;
    position: relative;
    top: 10px;
    right: -10px;
    table-layout: fixed;
    color: rgb(238, 220, 220);
}

table caption{
    font-size: 1.5em;
    font-weight: bold;
    margin: 0.5em 0;
    color:#fff
}

.table-cell {
  height: 15%;
  text-align: center;
  padding: 5px 0;
  font-size: 15px;
  font-family: 'Helvetica';
}

th,td{
    min-width:50px;
    max-width:200px;
    text-align: center;
    padding: 3px 0;
}

table thead tr{
    background-color: #453350;
    color: #fff;
}

table tbody tr:nth-child(odd){
    background-color:rgba(0,0,0,0.75)
}
tbody tr:nth-child(even) {
    background-color:rgba(0,0,0,0.50)
}
table tbody tr td:first-child{
    color:#2fa96c;
    font-weight: bold;
}


.button1 {
  display: inline-block;
  border-radius: 10px;
  background-color: #7d5b93;
  border: none;
  color: #FFFFFF;
  text-align: center;
  font-size: 15px;
  padding: 5px;
  width: 100px;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
  vertical-align:middle;
}

.button1 span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.button1 span:after {
  content: '»';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.button1:hover span {
  padding-right: 25px;
}

.button1:hover span:after {
  opacity: 1;
  right: 0;
}

}

@media screen and (max-width: 768px) {
    h2{
        font-size: 1em;
    }
    h3{
        font-size: 0.7em;
    }
    .ForceOrientationDiagram-container{
        width:100%;
