    }
    .lowerPart{
        width: 100%;
        height: 45%;
        overflow-y:scroll;
        overflow-x:hidden;
    }
    .table{
        width: 100%;
        height: 90%;
        margin-top: 1%;
        /* background-color: rgb(253, 253, 253); */
    }
    .force-orientedDiagram{
        width: 50%;
        height: 100%;
        float: left;
    }
    .graphicLinkagePart{
        width: 45%;
        height: 100%;
        float: right;
        margin-left: 5%;
    }
    .legends,.briefInformation{
        width: 100%;
        height: 45%;
        margin-top: 2%;
        margin-bottom: 2%;
        /* background-color: pink; */
        overflow-y:scroll;
        overflow-x:hidden;
        border: 4px solid;
        border-image: linear-gradient(to right, #8f41e9, #578aef) 1;
    }

    table{
        width: 100%;
        border-collapse: collapse;
    }

    table caption{
        font-size: 1em;
        font-weight: bold;
        margin: 0.5em 0;
        color:#fff;
    }


    th,td{
        max-width:200px;
        border: 1px solid #999;
        text-align: center;
        padding: 20px 0;
        font-size: 0.7em;
    }
    td{
        word-break: break-all; /* 单词打断 */
        word-wrap: break-word; /* 单词换行 */
    }
    table thead tr{
        background-color: #453350;
        color: #fff;
    }

    /* table tbody tr:nth-child(odd){
        background-color: #eee;
    } */
    table tbody tr{
        background-color: #eee;
    }
    table tbody tr:hover{
        background-color: #ccc;
    }

    table tbody tr td:first-child{
        color:#7d5b93;
        font-weight: bold;
    }


    .button1 {
    display: inline-block;
    border-radius: 10px;
    background-color: #7d5b93;
